#subs-page__landing {
  display: flex;
  overflow: hidden;
  flex-direction: column;
  box-shadow: none;
  @media (min-width: 1000px) {
    margin: 15vh auto 4rem;
    flex-direction: row;
    max-width: 960px;
    border-radius: 8px;
    box-shadow: 3px 6px 8px rgba(172,186,211,0.5); } }

.subs-page__landing__image {
  object-fit: cover;
  max-height: 512px;
  max-width: 60vw;
  margin: 0 auto 1rem;
  display: none;
  @media (min-width: 1000px) {
    display: block;
    max-width: 35%;
    min-width: 300px;
    flex: 1 0 35%;
    margin: 0; } }

.subs-page__landing__text {
  display: flex;
  flex-direction: column;
  padding: 80px 20px 5px;
  background-color: #F9F9F9;
  font-family: "proxima-nova", "Proxima Nova", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  @media (min-width: 1000px) {
    padding: 5px 20px; }
  .landing__text__heading {
    border-bottom: 1px solid #EEF1F6;
    @media (min-width: 640px) and (max-width: 1000px) {
      max-width: 640px;
      margin: 0 auto; }
    h1 {
      font-family: "proxima-nova", "Proxima Nova", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-size: 30px;
      line-height: 30px;
      color: #1A202C;
      position: relative;
      display: inline-block;
      margin: 2rem 0 1rem;
      &:after {
        content: '';
        width: 100%;
        height: 8px;
        background: linear-gradient(90deg, #4A5568, #E2E8F0);
        display: inline-block;
        position: absolute;
        bottom: -8px;
        left: 0;
        border-radius: 4px; } }
    ul {
      font-family: "proxima-nova", "Proxima Nova", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-size: 14px;
      line-height: 22px;
      padding-left: 20px;
      color: #4A5568; } }
  .landing__text__body {
    p {
      font-family: "proxima-nova", "Proxima Nova", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
      font-size: 16px;
      line-height: 22px;
      @media (min-width: 640px) and (max-width: 1000px) {
        max-width: 640px;
        margin: 2rem auto; } } } }
