#subs-page__form {
  width: 100vw;
  overflow: hidden;
  background-color: white;
  position: relative;
  counter-reset: heading-counter; }

#payment-form {
  position: relative;
  z-index: 2;
  margin: 8rem auto;
  transition: opacity 0.3s ease-in;
  &.locked {
    opacity: 0.4;
    pointer-events: none; }
  @media (min-width: 640px) {
    max-width: 640px; }
  @media (min-width: 1000px) {
    margin-top: 150px;
    margin-bottom: 150px;
    margin-left: calc(calc(calc(100vw - 960px) / 2) + 200px);
    padding-left: 20px;
    border-left: 2px solid #1A202C; }
  fieldset {
    margin: 0 0 8rem;
    padding: 2rem;
    color: #1A202C;
    border: none;
    background-color: #E2E8F0;
    position: relative;
    border-radius: 4px;
    legend {
      font-weight: bold;
      font-size: 1.8rem;
      counter-increment: heading-counter;
      padding: 0 8px 2px;
      background-color: #ffffff;
      &::before {
        content: counter(heading-counter) ". ";
        color: currentColor;
        font-weight: bold; } }
    .fieldset-inputs {
      display: flex;
      flex-wrap: wrap; }
    .fieldset-comment {
      @media (min-width: 1000px) {
        position: absolute;
        top: 0;
        left: -240px;
        max-width: 200px; }
      p {
        color: #4A5568;
        font-size: 12px;
        font-family: "proxima-nova", "Proxima Nova", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        margin: 8px 0;
        line-height: 16px;
        @media (min-width: 1000px) {
          text-align: right; } } }
    label {
      font-size: 12px;
      font-weight: bold;
      color: #363636;
      padding: 3px 0;
      margin-bottom: 2rem;
      display: block;
      &.full-width {
        width: 98%;
        flex: 0 0 98%; }
      &.half-width {
        @media (min-width: 640px) {
          width: calc(calc(100% - 24px) / 2);
          flex: 1 0 45%;
          margin-right: 12px; } }
      &.third-width {
        @media (min-width: 640px) {
          width: calc(calc(100% - 36px) / 3);
          flex: 1 0 31%;
          margin-right: 12px; } } }
    .validation {
      display: none;
      margin-top: 5px; }
    input {
      margin-right: 24px;
      width: 100%;
      font-size: 14px;
      line-height: 22px;
      font-weight: normal;
      color: #1A202C;
      padding: 4px 8px;
      border: none;
      background-color: #F9F9F9;
      border-radius: 4px;
      margin-top: 5px; }
    input[type="radio"] {
      transform: scale(0);
      position: absolute; }
    input[type="radio"] + label {
      border: 2px solid #1A202C;
      display: inline-block;
      padding: 12px 24px;
      border-radius: 4px;
      text-align: center;
      margin-right: 12px;
      font-size: 14px;
      color: #1A202C;
      &:hover {
        background-color: #1A202C;
        color: white;
        cursor: pointer; } }
    input[type="radio"]:checked + label {
      background-color: #1A202C;
      color: white; }
    input[type="text"]:required:valid, input[type="email"]:required:valid, {
      border: 1px solid #48BB78; }
    input[type="text"]:required:invalid, input[type="email"]:required:invalid, {
      border: 1px solid #F56565;
      & + .validation {
        display: block; } }
    input[type="text"]:required:placeholder-shown, input[type="email"]:required:placeholder-shown, input[type="email"]:required:focus {
      border: none;
      & + .validation {
        display: none; } }
    button {
      border: 2px solid #1A202C;
      display: inline-block;
      padding: 12px 36px;
      border-radius: 4px;
      text-align: center;
      font-size: 14px;
      background-color: rgba(255,255,255,0);
      font-weight: bold;
      margin: 0 auto 20px;
      @media (min-width: 1000px) {
        margin: auto;
        position: absolute;
        bottom: 1rem;
        right: 12px; }
      &:hover {
        background-color: #1A202C;
        color: white;
        cursor: pointer; } } } }

#card-element {
    background-color: #F9F9F9;
    padding: 12px 5px;
    border-radius: 4px;
    margin-right: 12px;
    margin-top: 5px; }

#sub_payment {
  label[for="card-element"] {
    margin-bottom: 0; } }

.stripe-info {
  color: #4A5568;
  font-size: 12px;
  font-family: "proxima-nova", "Proxima Nova", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 16px;
  margin: 20px 0;
  @media (min-width: 1000px) {
    margin: 8px 0 5rem; } }


#gifter_contact_details {
  border-top: 1px solid #CBD5E0;
  margin-top: 1rem;
  padding: 1rem 0; }

.form_modals {
  z-index: 999 !important; }

#success-container, #failure-container {
  max-width: 640px;
  margin: 100px auto;
  background-color: #f9f9f9;
  padding: 2rem;
  border-radius: 8px;
  box-shadow: 3px 6px 8px rgba(172,186,211,.5);
  display: block;
  &.invisible {
    display: none; }
  h3 {
    font-size: 2.2rem;
    text-align: center; }
  p {
    text-align: center;
    grid-column: none;
    font-family: "Proxima Nova", $sans;
    font-size: 16px;
    line-height: 22px;
    a {
      color: c('primary');
      &:hover {
        color: c('secondary'); } } } }
